<style scoped>
  .movie-card {
    height: 100%;
  }
  .movie-card img {
    height: 300px;
    object-fit: cover;
  }
  </style>

<template>
    <div class="container mt-5">
      <h1 class="text-center mb-4">
        Resultados da pesquisa para: <span>{{ pesquisa }}</span>
      </h1>
      <div id="movieList" class="row row-cols-1 row-cols-md-3 g-4">
        <div v-for="movie in movies" :key="movie.imdbID" class="col">
          <div class="card movie-card">
            <a
              :href="`https://www.imdb.com/title/${movie.imdbID}`"
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                :src="movie.Poster"
                class="card-img-top"
                :alt="movie.Title"
                @error="onImageError"
              />
            </a>
            <div class="card-body">
              <h5 class="card-title">{{ movie.Title }}</h5>
            </div>
          </div>
        </div>
      </div>
      <p v-if="error" class="text-center text-danger mt-4">{{ error }}</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        pesquisa: '',
        movies: [],
        error: null,
      };
    },
    mounted() {
      this.pesquisa = localStorage.getItem('pesquisa') || 'Nenhuma pesquisa realizada';
  
      document.title = this.pesquisa ? `Pesquisa IMDB - ${this.pesquisa}` : 'Pesquisa IMDB - Nenhum título encontrado';
  
      if (this.pesquisa !== 'Nenhuma pesquisa realizada') {
        this.fetchMovies();
      }
    },
    methods: {
      fetchMovies() {
        fetch(`https://api.collectapi.com/imdb/imdbSearchByName?query=${this.pesquisa}`, {
          method: 'GET',
          headers: {
            'content-type': 'application/json',
            authorization: 'apikey 4GVSSV51QMdmm7cbBSeMXi:4lccEU790kYCRqAL9rWzI2',
          },
        })
          .then((response) => response.json())
          .then((data) => {
            if (data.success && data.result.length > 0) {
              this.movies = data.result;
            } else {
              throw new Error('Nenhum resultado encontrado');
            }
          })
          .catch((error) => {
            this.error = `Erro ao carregar os filmes: ${error.message}. Por favor, tente novamente mais tarde.`;
            console.error('Erro ao buscar dados da API:', error);
          });
      },
      onImageError(event) {
        event.target.src = 'https://via.placeholder.com/300x450?text=No+Image';
      },
    },
  };
  </script>
  

  