<template>
  <div class="search-container">
    <h1 class="text-center">Trabalho de Rui</h1>
    
    <div class="input-group mb-3">
      <input type="text" class="form-control" v-model="pesquisa" placeholder="Digite o nome do filme ou série" />
      <button class="btn btn-primary" @click="clique">Pesquisar</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pesquisa: '',
    };
  },
  methods: {
    clique() {
      if (this.pesquisa.trim() !== "") {
        localStorage.setItem('pesquisa', this.pesquisa);
        this.$router.push('/pagina2'); 
      } else {
        alert("Por favor, insira um nome de filme ou série!");
      }
    }
  }
};
</script>

<style scoped>
.search-container {
  background-color: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

.results {
  margin-top: 20px;
}
</style>
